.mobile-menu-hidden {
  width: 70vw;
  height: 100vh;
  z-index: 10;
  background-color: var(--bg-menu);
  transition: transform 250ms var(--timing-function);
  flex-direction: column;
  padding-top: 60px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100vw);
}

@media screen and (min-width: 480px) {
