    width: 40vw;
    height: 100vh;
  }
}

.mobile-menu-hidden.is-open {
  transform: translateX(30vw);
}

@media screen and (min-width: 480px) {
  .mobile-menu-hidden.is-open {
    transform: translateX(50vw);
  }
}

@media screen and (min-width: 768px) {
