}

.menu-close-btn {
  stroke: var(--primary-text-color);
  cursor: pointer;
  transition: stroke 250ms var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.menu-close-btn:hover, .menu-close-btn:focus {
  stroke: var(--accent-color);
}

.backdrop {
  z-index: 10;
  width: 100%;
  height: 100%;
  visibility: visible;
  opacity: 1;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 250ms var(--timing-function), visibility 250ms var(--timing-function);
}

.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(1.2);
}

.modal {
  z-index: 10;
  width: 100%;
  opacity: 1;
  border: 3px solid var(--accent-color);
  transition: transform 250ms var(--timing-function), opacity 250ms var(--timing-function);
  background-color: #f7d0da;
  border-radius: 10px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

